.menu{
    position: relative;
    top: 30px;
    background-color: white;
    border-radius: 5px;
    width: 100px;
    box-shadow: 0px 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.menu > a{
    display: block;
    padding: 5px 8px;
    font-weight: 600;
    color: #494949;
    text-decoration: none;
}